import React, {useEffect, useState} from "react";


const Content = {
  eng: {
    title: 'Hello world',
    content: 'english text'
  },
  rus: {
    title: 'Привет мир',
    content: 'русский текст'
  }
}

const LanguageTag = {
  rus: 'rus',
  eng: 'eng'
}


export const Hello = ({}) => {

    const [isEng, setIsEng] = useState(false)
    const [languageTag, setLanguageTag] = useState(LanguageTag.rus)

    const onToggleLanguage = () => {
      setIsEng(!isEng)
    }

    useEffect(() => {

      if (isEng) {
        setContent(Content.eng)
      }
      else {
        setContent(Content.rus)
      }

    }, [isEng])

    const [content, setContent] = useState(Content.rus)

    const getTitle = () => {
      return Content.title[languageTag]
    }

    return (
        <div className={'hello'}>
            <div>
              <h1>
                {
                  content.title
                }
              </h1>

              <p>
                {
                  content.content
                }
              </p>
            </div>

          <button
            onClick={onToggleLanguage}
          >ENG</button>
        </div>
    )
}